gleSelect: false,\n      invoices_loading: false,\n      unallocated_payments_loading: false,\n      mpesa_payments_loading: false,\n      payment_methods: [],\n      outstanding_invoices: [],\n      unallocated_payments: [],\n      mpesa_payments: [],\n      selected_invoices: [],\n      selected_payments: [],\n      selected_mpesa_payments: [],\n      pos_profiles_list: [],\n      pos_profile_search: \"\",\n      payment_methods_list: [],\n      mpesa_searchname: \"\",\n      mpesa_search_mobile: \"\",\n      invoices_headers: [\n        {\n          text: __(\"Invoice\"),\n          align: \"start\",\n          sortable: true,\n          value: \"name\",\n        },\n        {\n          text: __(\"Customer\"),\n          align: \"start\",\n          sortable: true,\n          value: \"customer_name\",\n        },\n        {\n          text: __(\"Date\"),\n          align: \"start\",\n          sortable: true,\n          value: \"posting_date\",\n        },\n        {\n          text: __(\"Due Date\"),\n          align: \"start\",\n          sortable: true,\n          value: \"due_date\",\n        },\n        {\n          text: __(\"Total\"),\n          align: \"end\",\n          sortable: true,\n          value: \"grand_total\",\n        },\n        {\n          text: __(\"Outstanding\"),\n          align: \"end\",\n          sortable: true,\n          value: \"outstanding_amount\",\n        },\n      ],\n      unallocated_payments_headers: [\n        {\n          text: __(\"Payment ID\"),\n          align: \"start\",\n          sortable: true,\n          value: \"name\",\n        },\n        {\n          text: __(\"Customer\"),\n          align: \"start\",\n          sortable: true,\n          value: \"customer_name\",\n        },\n        {\n          text: __(\"Date\"),\n          align: \"start\",\n          sortable: true,\n          value: \"posting_date\",\n        },\n        {\n          text: __(\"Mode\"),\n          align: \"start\",\n          sortab